








export default {
    name: 'back-top',
    props: {

    },
    data () {
        return {
					show: false
        }
    },
    components: {

    },
		onPageScroll: function(res) {
			let _this = this;
			if(this.timeoutId){
				clearTimeout(this.timeoutId);
			}
			this.timeoutId = setTimeout(function(){ //节流
				res.scrollTop > 900 ? _this.show = true : _this.show = false;
				_this.timeoutId = undefined;
			},10);
		},
    methods: {
        backTop:function(){
					// this.show = false;
					uni.pageScrollTo({
						scrollTop: 0,
						duration: 400
					});
				}
    }
}
